        console.log('ondrop');
        var id = event.relatedTarget.getAttribute('item-id');
        var item = $filter('getById')(vm.awesomeThings, id);
        var index = vm.awesomeThings.indexOf(item);

        var areaType = event.target.getAttribute('drop-area');

